/* Above tyhe fold stuff goes here */
html, body, #page, #mast .row, #mast .fluid-container { height:100%; margin:0; }
body { width:100%; overflow-x: hidden; }
select[multiple] {
    height: 38px;
}

#page.support {
    height: auto;
}

#page.full {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.has-error {
    border-color: red;
}

.btn-lg, .btn-group-lg > .btn {
    border-radius: 0;
}

.embed-responsive img {
    width:100%;
}

/* Structure */
.container.no-gutter,
.container-fluid.no-gutter {
	padding-right: 0;
	padding-left: 0;
}

.container.no-gutter .row,
.container-fluid.no-gutter .row {
	margin-left: 0;
	margin-right: 0;
}

.no-gutter [class*='col-']:not(:first-child),
.no-gutter [class*='col-']:not(:last-child) {
	padding-right: 0 !important;
	padding-left: 0 !important;
}

/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

